import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {ProductService} from '../../services/store/product.service';
import {collection, doc, Firestore, getDoc, getDocs} from '@angular/fire/firestore';
import firebase from 'firebase/compat/app';
import {AuthService} from '../../services/auth/auth.service';
import {AngularFirestore} from '@angular/fire/compat/firestore';
import {AlertController, MenuController} from '@ionic/angular';
import {ChatService} from '../../services/chat/chat.service';

@Component({
  selector: 'app-check-status-store',
  templateUrl: './check-status-store.page.html',
  styleUrls: ['./check-status-store.page.scss'],
})
export class CheckStatusStorePage implements OnInit {

  percentage = 0;
  orderStatus = '';
  icon = '';
  option = [];
  options = [];
  private id = this.ionicAuthService.getUid();
  constructor(
    private router: Router,
    private product: ProductService,
    private firestore: Firestore,
    private ionicAuthService: AuthService,
    private afs: AngularFirestore,
    private menu: MenuController,
    private alertController: AlertController,
    private chatService: ChatService
  ) {     this.menu.enable(false);
  }
  async ionViewDidLeave() {
    await this.menu.enable(true);
  }

  async ngOnInit() {
    await this.changeIcon();
    await this.updateStatus();
    await this.getOptions();
  }

  async doRefresh(event) {
    await this.changeIcon();
    await this.updateStatus();
    setTimeout(() => {
      event.target.complete();
    }, 1000);
  }
  async updateStatus() {
    try {
      const orderRef = doc(this.firestore, `trackOrders/${(this.id)}/activeOrders/${(this.product.orderId)}`);
      const orderSnap = await getDoc(orderRef);
      const dataSnap = orderSnap.data();
      this.product.status = dataSnap.status;
      await this.product.changeStatus('status');
    }
    catch {
      await this.router.navigate(['track-orders']);
    }

  }

  async getOptions() {
    this.options = [];
    const opt = collection(this.firestore, `trackOrders/${(this.id)}/activeOrders/${(this.product.orderId)}/options`);
    const optSnapshot = await getDocs(opt);
    optSnapshot.forEach((docs) => {
      console.log(docs.id, ' => ', docs.data());
      this.options.push(
        {
          data: docs.data()
        },
      );
    });
    // eslint-disable-next-line guard-for-in
    for (const i in this.options) {
      const key = Object.keys(this.options[i].data);
      const value = Object.values(this.options[i].data);
      this.option.push(
        {
          name: key,
          value
        }
      );
    }
  }
  changeIcon() {
    switch (this.product.status) {
      case 'paid':
        this.icon = 'Start Preparing Order.';
        break;
      case 'prepare':
        this.icon = 'Ask to Pick Up.';
        break;
      case 'done':
        this.icon = 'Waiting for customer pick up.';
        break;
      case 'complete':
        this.icon = 'Order fulfilled.';
        break;
    }
  }

  // change user order status
  async informUser() {
    switch (this.product.status) {
      case 'paid':
        this.product.status = 'prepare';
        await this.afs.collection(`trackOrders/${(this.id)}/activeOrders`).doc(this.product.orderId).update({
          status: this.product.status,
          lastUpdate: firebase.firestore.FieldValue.serverTimestamp()
        });
        await this.afs.collection(`users/${(this.product.userId)}/activeOrders`).doc(this.product.orderId).update({
          status: this.product.status,
          lastUpdate: firebase.firestore.FieldValue.serverTimestamp()
        });
        await this.ngOnInit();
        break;
      case 'prepare':
        this.product.status = 'done';
        await this.afs.collection(`trackOrders/${(this.id)}/activeOrders`).doc(this.product.orderId).update({
          status: this.product.status,
          lastUpdate: firebase.firestore.FieldValue.serverTimestamp()
        });
        await this.afs.collection(`users/${(this.product.userId)}/activeOrders`).doc(this.product.orderId).update({
          status: this.product.status,
          lastUpdate: firebase.firestore.FieldValue.serverTimestamp()
        });
        await this.ngOnInit();
        break;
    }
  }

  async showAlertComplete(header, message) {
    const alert = await this.alertController.create({
      header,
      message,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'OK',
          handler: async data => {
            await this.informUser();
          }
        }
      ]
    });
    await alert.present();
  }

  back() {
    this.router.navigate(['track-orders']);
  }

  chat() {
    this.chatService.chatName = this.product.user;
    this.chatService.otherId = this.product.userId;
    this.router.navigate(['chat']);
  }
}
